<template>
  <div class="root">
    <header>
      <img
        class="head-img"
        :src="require('@/assets/images/speaking_bg.png')"
        alt=""
      />
    </header>
    <main>
      <div class="main-wrap">
        <div class="recent-box">
          <h2>近期新增宣讲数据汇总</h2>
          <p>数据时间{{handleStartTimeFormat(start_date)}}-{{handleEndTimeFormat(end_date)}}</p>
          <div class="flex">
            <div class="flex1 flex-center direction-column bor">
              <div class="num">{{total}}</div>
              <div class="char">新增场次</div>
            </div>
            <div class="flex1 flex-center direction-column">
              <div class="num">{{count}}</div>
              <div class="char">新增覆盖人数</div>
            </div>
          </div>
        </div>
        <div class="gap-char-wrap flex-center">
          <img
            class="char-img"
            :src="require('@/assets/images/speaking_char.png')"
            alt=""
          />
        </div>
        <div class="list-wrap">
          <div class="list-item" v-for="item in recentList" :key="item.id">
            <img
              class="bar-img"
              :src="require('@/assets/images/speaking_bar.png')"
              alt=""
            />
            <div class="message-box">
              <p>宣讲时间:{{handleTimeFormat(item.start_date)}}-{{handleLecEndTime(item.start_date, item.duration)}}</p>
              <h3>{{ item.title }}</h3>
              <div class="char">宣讲讲师：{{item.instructor_info.name}}</div>
              <div class="char">听课人数：{{item.visitor}}人</div>
              <div class="char">主办单位：{{item.host_info.name}}</div>
            </div>
          </div>
        </div>
        <p class="loc-char">“亲师友”提供技术支持</p>
      </div>
    </main>
  </div>
</template>

<script>
import dateformat from "dateformat";
import moment from "moment";
export default {
  data() {
    return {
      // 近期列表
      recentList: [],
      total: 0,
      count: 0,
      start_date: "2022-10-21",
      end_date: "2022-10-28",
    };
  },
  async created() {
    this.start_date = moment().isoWeekday(1).subtract(3, 'days').format("YYYY-MM-DD")
    this.end_date = moment().isoWeekday(7).subtract(2, 'days').format("YYYY-MM-DD")
    console.log("end_date",this.end_date)
    if (
      !this.$store.getters.associationInfo ||
      !this.$store.getters.associationInfo.id
    ) {
      await this.$store.dispatch("association/associationInfoDomainAPI");
    }
    this.recentListAPI();
  },
  methods: {
    handleTimeFormat(date) {
      return moment(date).utc().format("YYYY年MM月DD日 HH:mm")
      // return dateformat(date, "yyyy年mm月dd日 HH:MM")
    },
    handleStartTimeFormat(date) {
      return dateformat(date, "yyyy年mm月dd日")
    },
    handleEndTimeFormat(date) {
      return dateformat(date, "mm月dd日")
    },
    handleLecEndTime(date, duration) {
      return moment(date).add(duration, "seconds").utc().format("HH:mm")
    },
    /**
     * 近期列表API
     */
    async recentListAPI() {
      const aid = this.$store.getters.associationInfo.id;
      const data = {
        aid,
        start_date: this.start_date,
        end_date: this.end_date,
        limit: 10,
        page: 1,
      };
      const res = await this.$request("lecture.recentList2", data);
      console.log("近期列表API_res :>> ", res);
      this.recentList = res.data;
      this.total = res.total
      this.count = res.count
    },
  },
};
</script>

<style lang="scss" scoped>
.root {
  width: 100vw;
  height: 100vh;
  overflow: auto;
  background: #080f85;

  header {
    width: 100%;
    height: px(641);
    .head-img {
      width: 100%;
      height: 100%;
    }
  }
  main {
    background: #080f85;
    position: relative;
    .main-wrap {
      position: absolute;
      top: calc(px(426) - px(641));
      border-radius: px(40) px(40) 0px 0px;
      padding: px(40) px(24);
      background: #080f85;
      box-sizing: border-box;
      width: 100vw;
    }
    .recent-box {
      width: 100%;
      height: px(396);
      background: url("@/assets/images/recent_speaking_box.png");
      background-size: cover;
      padding-top: px(102);
      box-sizing: border-box;
      h2 {
        margin: 0;
        text-align: center;
        font-size: px(40);
        font-family: PingFang SC-Bold, PingFang SC;
        font-weight: bold;
        color: #333333;
      }
      p {
        margin-top: px(5);
        margin-bottom: px(24);
        text-align: center;
        font-size: px(24);
        font-family: PingFang SC-Light, PingFang SC;
        font-weight: 300;
        color: #333333;
      }
      .bor {
        border-right: 1px solid #eee;
      }
      .num {
        font-size: px(64);
        font-family: PingFang SC-Bold, PingFang SC;
        font-weight: bold;
        color: #0077ff;
      }
      .char {
        font-size: px(26);
        font-family: PingFang SC-Regular, PingFang SC;
        font-weight: 400;
        color: #999999;
      }
    }
    .gap-char-wrap {
      height: px(166);
      .char-img {
        width: px(344);
        height: px(67);
      }
    }
    .list-wrap {
      width: 100%;

      display: flex;
      flex-direction: column;
      align-items: center;
      .list-item {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        margin-bottom: px(64);

        .bar-img {
          width: 100%;
          height: px(64);
          position: absolute;
          top: px(-32);
          left: 0;
          opacity: 0.8;
        }
        .message-box {
          width: px(674);
          height: px(342);
          padding: px(32) 0 0 px(32);
          box-sizing: border-box;
          background: #ffffff;
          border-radius: px(8);
          p {
            font-size: px(24);
            font-family: PingFang SC-Regular, PingFang SC;
            font-weight: 400;
            color: #999999;
            margin-bottom: px(48);
          }
          h3 {
            font-size: px(32);
            font-family: PingFang SC-Bold, PingFang SC;
            font-weight: bold;
            color: #333333;
            margin-bottom: px(16);
          }
          .char {
            font-size: px(28);
            font-family: PingFang SC-Regular, PingFang SC;
            font-weight: 400;
            color: #999999;
            margin-bottom: px(16);
          }
        }
      }
    }
  }
  .loc-char {
    font-size: px(24);
    font-family: PingFang SC-Regular, PingFang SC;
    font-weight: 400;
    color: #cccccc;
    text-align: center;
  }
}
</style>